
import { reactive, watch, onMounted } from "vue";
import { useRouter, useRoute, RouteRecordRaw } from "vue-router";
// 引入ts接口
import { layoutSetting, animationUpDown } from "./types/layout";
export default {
  name: "app",
  setup(): unknown {
    // 页面设置相关
    let pageSetting = reactive<layoutSetting>({
      // 设置当前展示页
      activeIndex: 1,
      // 设置头部header显示与隐藏
      isHeader: false,
      // 控制滚轮的节流阀
      settimer: true,
      // 动画上下初始控制
      AnimationUpDown: animationUpDown.fade,
    });
    // 获取的路由对象
    const Router = useRouter(),
      Route = useRoute();

    // 监视路由的变化
    watch(
      () => Route.meta,
      (newValue) => {
        // 控制页面title展示
        document.title = newValue.title as string;

        //  控制顶部header和malk展示
        pageSetting.isHeader =
          newValue.index != 1 && newValue.index != 7
            ? true
            : false;
        // 统一index
        pageSetting.activeIndex = newValue.index as number;
      },
      { immediate: true }
    );

    onMounted(() => {
      document.body.onkeydown = function (event) {
        event.preventDefault();
      };
    });

    // 鼠标滚动事件
    function wheelCheckout(e: WheelEvent): void {
      if (pageSetting.settimer) {
        // 开启节流
        pageSetting.settimer = false;

        if (e.deltaY > 0) {
          pageSetting.activeIndex =
            pageSetting.activeIndex >= Router.options.routes.length - 2
              ? Router.options.routes.length - 2
              : pageSetting.activeIndex + 1;

          pageSetting.AnimationUpDown = animationUpDown.down;
        } else {
          pageSetting.activeIndex =
            pageSetting.activeIndex <= 1 ? 1 : pageSetting.activeIndex - 1;
          pageSetting.AnimationUpDown = animationUpDown.up;
        }

        // 跳转至对应页面
        routerJump(Router.options.routes as Array<RouteRecordRaw>);

        // 关闭节流阀
        setTimeout(() => {
          pageSetting.settimer = true;
        }, 1500);
      }
    }

    // 路由跳转方法
    function routerJump(routes: Array<RouteRecordRaw>): void {
      routes.forEach((item) => {
        if (item.meta?.index == pageSetting.activeIndex) {
          Router.push(item.path);
        }
      });
    }

    function onPush(item: RouteRecordRaw) {
      Router.push({
        path: item.path,
      });
    }

    let routes = Router.options.routes.slice(0, -2);

    return { pageSetting, wheelCheckout, routes, onPush };
  },
};
